import { useEffect, useCallback } from 'react';
/**
 * ! Call this hook inside Shadow Root!
 */
export function useCapturedInput(ref, onChange) {
    const stop = useCallback((e) => e.stopPropagation(), []);
    const use = useCallback((e) => onChange(e.currentTarget.value), [onChange]);
    function binder(keys, fn) {
        return () => {
            if (!ref.current)
                return;
            keys.forEach(k => ref.current.addEventListener(k, fn, true));
            return () => {
                if (!ref.current)
                    return;
                keys.forEach(k => ref.current.removeEventListener(k, fn, true));
            };
        };
    }
    useEffect(binder(['keydown', 'keyup', 'keypress', 'change'], use), [ref.current]);
    useEffect(binder([
        'paste',
        'keydown',
        'keypress',
        'keyup',
        'input',
        'drag',
        'dragend',
        'dragenter',
        'dragexit',
        'dragleave',
        'dragover',
        'dragstart',
        'change',
    ], stop), [ref.current]);
}
